.add-favorite {
  background-color: transparent;
  position: absolute;
  top: 5%;
  right: 5%;
  width: 20%;
}

.add-favorite img {
  width: 100%;
}

.add-favorite:hover .normal-star {
  display: none;
}

.add-favorite:hover .purple-star {
  display: inline-block;
}

.card {
  position: relative;
  width: 100%;
  aspect-ratio: 340/334;
  flex-shrink: 0;
  border-radius: 15px;
  box-shadow: 0px 5px 25px 0px rgba(0, 0, 0, 0.08);
  display: flex;
  flex-direction: column;
  overflow: visible;
}

.card:hover {
  scale: 1.1;
}

.card-info {
  width: 100%;
  height: 40%;
  background-color: var(--white-color);
  display: flex;
  flex-direction: column;
  padding: 8px 16px;
  border-bottom-right-radius: 15px;
  border-bottom-left-radius: 15px;
  gap: 4px;
}

.card-info-bar {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  align-self: stretch;
}

.card-info-date {
  color: #333;
  text-overflow: ellipsis;
  white-space: nowrap;
  font-size: 0.625em;
  line-height: normal;
}

.card-info-text {
  display: -webkit-box;
  overflow: hidden;
  color: #000;
  margin-top: -0.3125em;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  font-size: 1em;
  line-height: 1.25em;
}

.card-info-time {
  color: var(--grey-dark-color);
  font-size: 0.625em;
  line-height: normal;
}

.card-row {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  width: fit-content;
  gap: 1.125em;
  height: 100%;
  align-items: center;
}

.card-selected {
  border: 3px solid var(--primary-color);
}

.container {
  background-color: var(--white-color);
  display: flex;
  width: 998px;
  height: 450px;
  gap: 157px;
}

.description {
  color: #6b6b6b;
  margin-right: 0.625em;
  font-family: Abel;
  font-size: 1em;
  line-height: 150%;
}

.feature-description {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 10px;
}

.feature-example-image {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  flex-shrink: 0;
  background-color: var(--d1-gray-color);
  width: 55%;
  aspect-ratio: 55/45;
  border-radius: 15px;
}

.folder-add {
  color: var(--primary-color);
}

.folder-btn {
  width: 100%;
}

.folder-input {
  display: flex;
  width: 280px;
  padding: 18px 15px;
  justify-content: center;
  align-items: center;
  border-radius: 8px;
  border: 1px solid var(--Linkbrary-gray20, #ccd5e3);
  background: var(--white-color);
}

.folder-input::placeholder {
  color: var(--d4-gray-color);
  font-size: 1em;
  line-height: 1.5em;
}

.folder-change {
  background-color: var(--white-color);
  border-radius: 15px;
  border: 1px solid var(--d3-gray-color);
  display: inline-flex;
  padding: 35px 40px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 24px;
  box-shadow: 0px 5px 25px 0px rgba(0, 0, 0, 0.08);
  z-index: 1;
}

.folder-name {
  color: var(--d4-gray-color);
  text-align: center;
  font-size: 0.875em;
  font-style: normal;
  font-weight: 400;
  line-height: 1.375em;
}

.folder-share {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 8px;
  width: 280px;
}

.feature-description-heading {
  color: #000;
  font-size: 3em;
  font-weight: 700;
  line-height: normal;
  letter-spacing: -0.3px;
}

.folder-share-link {
  display: flex;
  justify-content: space-between;
  gap: 32px;
}

.feature-description-section {
  background-color: var(--white-color);
  margin: -120px 0 0;
  padding: 120px 0px;
  gap: 100px;
  flex: 1;
}

.gradient1 {
  background: linear-gradient(91deg, #6d6afe 17.28%, #ff9f9f 74.98%);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.gradient2 {
  background: linear-gradient(96deg, #fe8a8a 1.72%, #a4ceff 74.97%);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.gradient3 {
  background: linear-gradient(277deg, #6fbaff 19.22%, #ffd88b 93.66%);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.gradient4 {
  background: linear-gradient(
    99deg,
    #6d7ccd 69.76%,
    rgba(82, 136, 133, 0.22) 99.69%
  );
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.gradient5 {
  background: var(
    --Fandom-K-gra-blue-to-pink,
    linear-gradient(271deg, #fe578f -9.84%, #68e8f9 107.18%)
  );
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.info-link-bar ul {
  display: flex;
  gap: 12px;
}

.kebab {
  width: 12px;
  aspect-ratio: 12/10;
}

.link-add {
  width: 350px;
}

.link-list {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.link-list-bar {
  display: flex;
  justify-content: space-between;
}

.link-cards {
  width: 100%;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 20px;
}

.logo {
  width: 150px;
  height: 24px;
}

.mobile-description {
  display: none;
}

.no-shrink-card {
  width: 244px;
  flex-shrink: 0;
}

.op-font {
  color: #373740;
  font-size: 1.25em;
  font-weight: 700;
  line-height: normal;
}

.page-sample {
  position: relative;
  width: 100%;
  flex-direction: column;
  align-items: center;
}

.page-sample-content {
  position: relative;
  padding: 20px 10%;
  max-width: 1118px;
  width: 58.2291%;
  aspect-ratio: 1118/659;
  border-radius: 25px;
  border-radius: 25px;
  background: var(--white-color);
  z-index: -1;
  box-shadow: 0px 4px 25px 0px rgba(0, 0, 0, 0.08);
  flex-direction: column;
  flex-grow: 1;
  gap: 20px;
}

.page-sample-dim-effect {
  position: absolute;
  top: 0;
  max-width: 1168px;
  width: 60.83%;
  height: calc(100% - 120px);
}

.page-sample-section {
  position: relative;
  background-color: inherit;
  padding-top: 70px;
  overflow: hidden;
  gap: 40px;
}

.purple-star {
  display: none;
}

.save-icon {
  background-color: transparent;
  position: absolute;
  bottom: -23px;
  right: -23px;
  z-index: 1;
}

.search-bar {
  background-color: var(--grey-light-color);
  border-radius: 10px;
  padding: 15px 16px;
}

.search-box {
  display: flex;
  flex-direction: column;
  position: absolute;
  top: 55px;
  left: 60px;
  border-radius: 15px;
  background: linear-gradient(
    180deg,
    rgba(255, 255, 255, 0) 48.72%,
    #000 128.17%
  );
  background-color: var(--white-color);
  box-shadow: 0px 4px 25px 0px rgba(0, 0, 0, 0.08);
  width: 619px;
  height: 584px;
  flex-shrink: 0;
  padding: 60px 40px;
  gap: 30px;
}

.search-input {
  width: 100%;
  background-image: url("../img/Search.svg");
  background-repeat: no-repeat;
  background-position: left center;
  font-size: 1em;
  background-color: inherit;
  padding: 0 32px;
  color: var(--grey-dark-color);
  border: none;
}

.search-result {
  font-size: 2em;
  margin-bottom: 10px;
}

.select-all-btn {
  background-color: var(--primary-color);
  color: var(--white-color);
}

.select-favorite-btn {
  border: 1px solid var(--primary-color);
}

.select-folder-btn {
  border: 1px solid var(--primary-color);
}

.share-bg {
  position: absolute;
  width: 100%;
  height: 100%;
  filter: brightness(0.5);
  z-index: 0;
}

.share-link-btn {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 10px;
}

.share-link-btn a img {
  border-radius: 50%;
}

.share-link-btn a img:hover {
  box-shadow: 0 0 10px var(--skyblue-color);
}

.share-link-logo {
  width: 42px;
  aspect-ratio: 1/1;
}

.share-link-name {
  color: #444;
  text-align: center;
  font-family: Inter;
  font-size: 0.8125em;
  font-style: normal;
  font-weight: 600;
  line-height: 15px;
}

.svg-change {
  transition: fill 0.3s ease;
}

.svg-change:hover {
  fill: var(--primary-color);
}

.thumbnail {
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  flex-grow: 1;
}

.thumbnail img {
  border-radius: inherit;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.top-heading {
  color: #000;
  font-size: 4em;
  font-style: normal;
  font-weight: 700;
  line-height: 80px;
  width: 708px;
  text-align: center;
}

.yellow-star {
  color: rgb(249, 249, 78);
}
